function safeCourse() { }

safeCourse.pageReady = function () {
    Ext.getCmp('btnCloseClass').setDisabled(true);
    Ext.getCmp('pnClassInfo').setDisabled(true);
    Ext.getCmp('btnAddCertificate').setDisabled(true);
}

safeCourse.grdSafeClass_onSelectionChange = function (item, selected) {
    if (selected[0]) {
        var record = selected[0]
        , classId = record.get("ClassId");

        App.direct.grdSafeClass_onSelectionChange(classId, {
            eventMask: {
                showMask: true,
                msg: "Vui lòng chờ..."
            },
            success: function () {
                Ext.getCmp('btnCloseClass').setDisabled(false);
                Ext.getCmp('pnClassInfo').setDisabled(false);
                Ext.getCmp("grdLearnerList").setDisabled(false);
                Ext.getCmp('btnAddCertificate').setDisabled(true);

                Ext.getCmp('frClassInfo').getForm().loadRecord(record);
            }
        });
    }
}

safeCourse.grdLearnerList_onSelectionChange = function (item, selected) {
    if (selected.length == 0) {
        Ext.getCmp('btnAddCertificate').setDisabled(true);
    }
    else /* if (selected.length > 0) */ {
        Ext.getCmp('btnAddCertificate').setDisabled(false);
    }
}

safeCourse.btnRemoveLearner_onClick = function (item, e) {
    var classId = Ext.getCmp("hdClassId").getValue()
    , record = this.record
    , studentCode = record.get("WORKERCODE");

    App.direct.btnRemoveLearner_onClick(classId, studentCode, {
        eventMask: {
            showMask: true,
            msg: "Vui lòng chờ..."
        },
        success: function () {
            Ext.getStore("stLearnerList").remove(record);
        }
    });
}

safeCourse.btnAddClass_onClick = function (item, e) {
    Ext.getCmp("grdSafeClass").setDisabled(true);
    Ext.getCmp("pnClassInfo").setDisabled(false);
    Ext.getCmp("grdLearnerList").setDisabled(true);
    Ext.getCmp("btnSaveClassInfo").setDisabled(false);

    Ext.getCmp("frClassInfo").getForm().reset();
    Ext.getCmp("grdLearnerList").getStore().removeAll();
    Ext.getCmp("tfClassName").focus();
}

safeCourse.btnResetClassInfo_onClick = function (item, e) {
    Ext.getCmp("grdSafeClass").setDisabled(false);
    Ext.getCmp("pnClassInfo").setDisabled(true);

    Ext.getCmp("frClassInfo").getForm().reset();
    Ext.getCmp("cnlClassInfo_DateRange").resetValue();
    Ext.getCmp("grdSafeClass").getSelectionModel().deselectAll();
}

safeCourse.btnAddLearner_onClick = function (item, e) {
    var classId = Ext.getCmp("hdClassId").getValue()

    App.direct.btnAddLearner_onClick(classId, {
        eventMask: {
            showMask: true,
            msg: "Vui lòng chờ..."
        },
        success: function () {
            Ext.getCmp("winNomineeStudent").show();
        }
    })
}

safeCourse.grdNomineeStudent_onSelectionChange = function (item, selected) {
    var field = Ext.getCmp("tagSelectedStudent");

    if (selected.length > 0) {
        selected.forEach(function (record, i) {
            var data = {
                value: record.get("WORKERCODE"),
                text: record.get("NAME"),
                iconCls: "#UserAdd"
            };

            field.addTag(data);
        })
    }
}

safeCourse.grdNomineeStudent_tfSearch_onClick = function (field, trigger, index, callback) {
    var me = field
    , classId = Ext.getCmp("hdClassId").getValue();

    if (index == 0) {
        me.setValue('');
    }

    App.direct.grdNomineeStudent_forSearch(classId, me.getValue(), {
        eventMask: {
            durationMessages: [{
                duration: 300,
                message: "Vui lòng chờ..."
            }]
        },
        success: function () {
            if (callback) {
                callback();
            }
        },
        failure: function (err) {
            if (callback) {
                callback(err);
            }
        }
    })
}

safeCourse.grdNomineeStudent_tfSearch_onKeyPress = function (field, e) {
    if (e.getKey() == e.ENTER) {
        safeCourse.grdNomineeStudent_tfSearch_onClick(field, null, 1, function (err) {
            if (err) {
                alert('Error: ' + err);
            }

            e.stopEvent();
        });
    }
}

module.exports = safeCourse;


